@import url(https://stackpath.bootstrapcdn.com/bootstrap/4.4.1/css/bootstrap.min.css);
.product--card {
  background-color: #fff;
  margin: 15px auto;
  padding: 35px 40px;
  box-sizing: border-box;
  border-radius: 15px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
}
.product--card h4 {
  font-weight: bold;
}
.product--card .product--card-description p {
  color: #8e8e8e;;
}
.product--card-style p {
  padding: 0; margin: 0;
  color: #5595d7;
}
.product--card-delivery p {
  text-align: right;
  font-weight: bold;
  text-decoration: underline;
  color: #2779cd;;
}
.product--card-price p {
  color: #ff9f21;;
}
/* simple reset */
* {
  padding: 0;
  margin: 0;
}

body {
  background-color: #fbfbfb;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial,
    sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

/* header */
.header {
  width: 100%;
  min-height: 250px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #007bff;
}
.header--search {
  width: 40%;;
  padding: 10px 15px;
  border-radius: 10px;
  border: none;
  outline: none;;
}

/* products */
.list--products {
  padding: 40px 0;
}

